import React, { Component } from 'react';

import Dialog from 'react-dialog'
import { saveSchedule } from '../httpClient';

class DialogBox extends Component {
  constructor(props) {
    super(props);

    this.saveShedule = this.saveShedule.bind(this);
  }

  saveShedule() {
      const obj = {
        startDate: document.getElementById('StartDate').value,
        endDate: document.getElementById('EndDate').value,
        customerId: this.props.customerId,
        tiffinType: document.getElementById('TiffinType_Shedule').value,
        isWeekend: document.getElementById('Yes').checked
    }

    saveSchedule(obj).then(function(schedule) {
        console.log("Schedule.............", schedule);
    });
  }

  render() {
    return (
        <Dialog
            title="Dialog Title"
            modal={true}
            onClose={this.props.handleClose}
            buttons={
                [{
                    text: "Close",
                    onClick: () => this.props.handleClose()
                }]
            }>
            <h1>Hello..................</h1>
            {this.props.customerId} <br />

            Start Date: <input type='date' name='startDate' id='StartDate' /> <br />
            End Date: <input type='date' name='endDate' id='EndDate' /> <br />
            Tiffin Type: <select id='TiffinType_Shedule'>
                                <option value="1">Launch</option>
                                <option value="2">Dinner</option>
                                <option value="3">Both</option>
                              </select> <br /> 

            <p>Include Weekends:</p>

            <div>
                <input type="radio" id="Yes" name="drone" value="yes"
                        checked />
                <label for="yes">Yes</label>
            </div>
            <div>
                <input type="radio" id="No" name="drone" value="no" />
                <label for="no">No</label>
            </div><br /><br />
            
            <input type='button' name='saveSchedule' id='SaveShedule' value='Save' onClick={() => this.saveShedule()}/>
        </Dialog>
        )
    }
}

export default DialogBox;